
.animate{-webkit-animation-duration:1s;animation-duration:1s
}
.animate--grow{-webkit-animation-name:grow;animation-name:grow;-webkit-animation-duration:300ms;animation-duration:300ms
}
.animate--top-enter{-webkit-animation-name:topEnter;animation-name:topEnter;-webkit-animation-duration:300ms;animation-duration:300ms
}
.animate--top-leave{-webkit-animation-name:topLeave;animation-name:topLeave;-webkit-animation-duration:300ms;animation-duration:300ms
}
@-webkit-keyframes grow{
0%{opacity:0;-webkit-transform-origin:50% 50%;transform-origin:50% 50%;-webkit-transform:scale(0.5);transform:scale(0.5)
}
100%{opacity:1;-webkit-transform-origin:50% 50%;transform-origin:50% 50%;-webkit-transform:scale(1);transform:scale(1)
}
}
@keyframes grow{
0%{opacity:0;-webkit-transform-origin:50% 50%;transform-origin:50% 50%;-webkit-transform:scale(0.5);transform:scale(0.5)
}
100%{opacity:1;-webkit-transform-origin:50% 50%;transform-origin:50% 50%;-webkit-transform:scale(1);transform:scale(1)
}
}
@-webkit-keyframes topEnter{
0%{opacity:0;-webkit-transform:translate(0, -100%);transform:translate(0, -100%)
}
100%{opacity:1;-webkit-transform:translate(0, 0);transform:translate(0, 0)
}
}
@keyframes topEnter{
0%{opacity:0;-webkit-transform:translate(0, -100%);transform:translate(0, -100%)
}
100%{opacity:1;-webkit-transform:translate(0, 0);transform:translate(0, 0)
}
}
@-webkit-keyframes topLeave{
0%{opacity:1;-webkit-transform:translate(0, 0);transform:translate(0, 0)
}
100%{opacity:0;-webkit-transform:translate(0, -100%);transform:translate(0, -100%)
}
}
@keyframes topLeave{
0%{opacity:1;-webkit-transform:translate(0, 0);transform:translate(0, 0)
}
100%{opacity:0;-webkit-transform:translate(0, -100%);transform:translate(0, -100%)
}
}
.container{max-width:760px;margin:0 auto;padding:20px;width:100%
}
.container__no-padding{padding:0
}
.container__centered{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;min-height:100%;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column
}
.container__padding{padding-top:300px
}
.button{display:inline-block;font:inherit;font-weight:800;border:0;padding:15px 30px;border-radius:3px;outline:none;cursor:pointer;background-color:#eee;-webkit-box-shadow:0 5px #d0d0d0;box-shadow:0 5px #d0d0d0;-webkit-transition:-webkit-box-shadow 300ms;transition:-webkit-box-shadow 300ms;transition:box-shadow 300ms;transition:box-shadow 300ms, -webkit-box-shadow 300ms;text-decoration:none
}
.button:hover{top:2px;position:relative;-webkit-box-shadow:0 3px #d0d0d0;box-shadow:0 3px #d0d0d0
}
.button--yellow{color:#fff;background-color:#fad468;-webkit-box-shadow:0 5px #f3bc65;box-shadow:0 5px #f3bc65
}
.button--yellow:hover{-webkit-box-shadow:0 3px #f3bc65;box-shadow:0 3px #f3bc65
}
.dlist{margin-bottom:20px
}
.dlist__title{font-weight:800;margin-bottom:5px
}
.dlist__body{margin-bottom:10px;overflow-wrap:break-word
}
*{-webkit-box-sizing:border-box;box-sizing:border-box
}
body,html{height:100%
}
body{font-family:"Avenir", Helvetica, Arial, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:#2c3e50;margin:0;background:#457fca;background:-webkit-gradient(linear, right top, left top, from(#457fca), to(#5691ca));background:linear-gradient(to left, #457fca, #5691ca)
}

.message{position:fixed;background-color:#ffffff;height:80px;width:100%;font-weight:500
}

.urlform{width:100%;margin-bottom:40px
}
.urlform__input{font:inherit;font-size:1.2em;padding:20px;width:100%;border:0;border-radius:3px;-webkit-box-shadow:0 0 50px rgba(255,255,255,0.1);box-shadow:0 0 50px rgba(255,255,255,0.1)
}
.notice{color:#fff;font-size:1.1em;font-weight:500
}
.result{width:100%;text-align:center
}
.result__details{color:#fff;font-size:1.4em
}
.result__url{display:block;margin-bottom:20px;color:#fff
}
.result__original{opacity:0.5;margin-bottom:5px;display:-webkit-box;display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center
}
.result__original span{display:inline-block;max-width:300px;overflow:hidden;white-space:nowrap;text-overflow:ellipsis;margin-right:8px
}
.result__stats{display:block;color:#fff;font-size:0.9em;margin-bottom:20px
}
@media (min-width: 736px){
.result{display:-webkit-box;display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center
}
.result__stats{margin-bottom:0
}
.result__details{margin-right:40px
}
}

.notfound{color:#fff;font-size:1.2em;font-weight:500
}

.stats{color:#fff;font-size:1.2em;font-weight:500;width:100%
}
.stats__header{font-size:1.4em;margin:0;margin-bottom:40px;overflow-wrap:break-word
}
.stats__loading{color:#fff;font-size:1.2em;font-weight:500
}
